import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../../actions/datamaster_action";
import { ReanderField } from "../../../components/helpers/field";
import ValidasiMasterKodeTransaksi from "../../../Validasi/ValidasiMasterKodeTransaksi.jsx";
import Loading from 'react-fullscreen-loading';

const maptostate = (state) => {
  if (state.datamaster.ShowModalKodeTransaksi !== null) {
    return {
      initialValues: {
        kode_transaksi: state.datamaster.ShowModalKodeTransaksi.kode_transaksi,
        kode_transaksi_lama:
          state.datamaster.ShowModalKodeTransaksi.kode_transaksi,
      },
    };
  }
};

class FormModalKodeTrransaksi extends Component {
  state = {
    aktif: false,
  };
  componentDidMount() {
    setTimeout(() => {
      document.getElementById("kode_transaksi").focus();
    }, 100);
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        autoComplete="off"
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
      >
        <input type="hidden" name="kode_transaksi_lama" />
        <Field
          tabIndex="1"
          id="kode_transaksi"
          name="kode_transaksi"
          component={ReanderField}
          type="text"
          label="Kode Transaksi"
          placeholder="Masukan Kode Transaksi"
          onBlur={() => this.setState({ aktif: true })}
        />
        {/* <Select options={options} /> */}

        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            &nbsp;
            <button
              tabIndex="2"
              className={this.state.aktif ? "btn btn-primary" : "btn btn-white"}
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

FormModalKodeTrransaksi = reduxForm({
  form: "ModalKodeTrransaksi",
  enableReinitialize: true,
  validate: ValidasiMasterKodeTransaksi,
})(FormModalKodeTrransaksi);
export default connect(maptostate, null)(FormModalKodeTrransaksi);
